:root {
  --local-red: hsl(0, 100%, 74%);
  --local-green: hsl(154, 59%, 51%);
  --light-green: #77e2b4;
  --local-blue: hsl(248, 32%, 49%);
  --local-dark-blue: hsl(249, 10%, 26%);
  --local-grayish-blue: hsl(246, 25%, 77%);
}

/* || BOX MODEL FIX */
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* || CENTERING MAIN IN BODY */

body {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 100vh;
}

main {
  width: 100%;
  max-width: 85%;
  padding: 2.5rem;
  margin: auto;
}

footer {
  align-self: flex-end;
  margin: 0 auto;
}

/* || GLOBAL STYLES */

body {
  min-width: 400px;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  color: white;
  text-align: center;
}

body {
  background-color: var(--local-red);
  background-image: url(images/bg-intro-mobile.png);
  /* background-repeat: no-repeat; */
  background-attachment: fixed;
}

h1 {
  padding-bottom: 1rem;
}

/* || TRY IT FREE */

.tryItFree {
  background-color: var(--local-blue);
  border-radius: 1rem;
  padding: 1rem;
  margin-top: 4rem;
  margin-bottom: 2rem;
}

.tryItFree {
  box-shadow: 0px 8px 4px 0px rgb(0 0 0 / 40%);
}

#tryItFreeText {
  font-weight: bold;
}

/* || ATTRIBUTION */
.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

/* || ACTIVE STATES */

#submitButton:hover {
  cursor: pointer;
  background-color: var(--light-green);
}

/* || RESPONSIVE DESIGN: DESKTOP */
@media screen and (min-width: 1100px) {
  /* @media screen and (min-width: 1250px) { */
  main {
    padding-bottom: 7rem;
  }
  main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    column-gap: 10%;
  }
  .grid-left {
    text-align: left;
  }
}
